<template>
  <div class="backimg">
    <form @submit.prevent="login">
      <div class="img"></div>
      <div class="d-flex">
        <label for="log-text" class="log-icon log-icon1"></label>
        <input type="text" id="log-text" v-model="urse.name" placeholder="请输入账号" />
      </div>
      <div class="d-flex">
        <label for="log-psw" class="log-icon log-icon2"></label>
        <input type="password" id="log-psw" v-model="urse.pwd" placeholder="请输入密码" />
      </div>

      <input type="submit" placeholder="登录" />
      <p v-show="zhuang" style="color:#f00">{{zhuang}}</p>
    </form>
  </div>
</template>

<script>
// @ is an alias to /src
import Message from "element-ui";
import { mapMutations } from "vuex";
import { log } from "util";
export default {
  name: "home",
  data() {
    return {
      urse: {
        name: "",
        pwd: ""
      },
      zhuang: ""
    };
  },
  created() {},
  methods: {
    ...mapMutations(["changeLogin"]),
    login() {
      let _this = this;
      if (this.urse.name && this.urse.pwd) {
        this.axios({
          url: "/api/a/login",
          method: "post",
          data: `name=${this.urse.name}&pwd=${this.urse.pwd}`,
          emulateJSON: true
        })
          .then(res => {
            if (res.data.message == "success") {
              _this.changeLogin({ iflogin: true });
              // console.log( _this.changeLogin)
              // if(this.$router.query.redirect==location.hostname){
              //   // this.$router.go(-1)
              //   this.$router.push('/home/123')
              // }else{
              this.$router.push("/home");
              // }
              // this.$router.go(-1);
              // router.push({path:decodeURIComponent(url)});
            } else {
              this.zhuang = res.data.message;
            }
          })
          .catch(err => console.log(err));
      } else {
        alert("请输入用户名和密码");
      }
    }
  }
};
</script>
